header{
    z-index:200;
    min-height: 120vh;
    background-color: rgb(61, 90, 254);
    background-image: url("./img/-mountain.webp");
    background-position: center top;
    background-repeat: no-repeat;
    background-size: cover;
    
    .headercontainer{
        height: 90vh;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .getintouch{
        display: flex;
        justify-content: flex-end;
    }
    .headercontent{
        display: flex;
        flex-direction: column;
        .dan {
            text-transform: uppercase;
            h1{
                font-size: 4rem;
                color: white;
            }
            h2 {
                font-size: 2rem;
                color: white;
            }
            .links{
                margin: 2.5rem auto;
                a {
                    padding: 20px;
                }
            }
        } 
    }
    .scrollDown{
        display: flex;
        justify-content: center;
    }
    .scrollTop{
        display: none;
        img{
            border-radius: 50%;
            background-color: rgb(140, 158, 255);
            padding: 10px;
        }
        
        z-index:2000;
        position: fixed;
        bottom: 60px;
        right: 30px;
    }
    @media (min-width: 768px) {
        .scrollTop {
            display: block;
        }
    }

}
.section {
    padding: 1rem .5rem;

}
@media (min-width: 768px){
    .section{
        padding: 3rem 1.5rem;
    }
    .scrollTop{
        display: block;
    }
}